section{
    width: 400px;
    height: 300px;
    margin: 50px;
    border: {
        width: 2px;
        style: solid;
        radius: 30px;
    }
}

.box{
    width: 100px;
    height: 100px;
    background-color: #000;
    color: #fff;
    line-height: 100px;
    text-align: center;
    font-size: 4em;
    margin: 20px;
}

.box{
    animation-name: somename;
    animation-duration: 3s;
    animation-iteration-count: 3;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    
}

@keyframes somename {
    50%{
        background-color: #f11;
    
    }

    70%{
        border-radius: 50%;
    }

    100%{
        rotate: 360deg;
    }   
}

button{
    width: 300px;
    height: 80px;
    background-color: #f11;
    color: #fff;
    font-size: 3em;
    animation-duration: 100ms;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-direction: alternate;
}

button:hover{
    animation-name: shake;
}

@keyframes shake {
    from{
        scale:0.95;
    }

    to{
        scale:1.05;
    }
    
}